button {
  background: rgba(0,0,0,.075);
  border-radius: 100px;
  padding: 7px 30px;
  color: #eb7203;
  text-decoration: none;
  font-size: 2em;
  margin: 0 15px;
  border-color:#eb7203;
  border-style: solid;
  border-width: 4px;
}

/* Grow */
.hvr-grow {
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    -webkit-box-shadow: 0 0 1px rgba(0, 0, 0, 0);
            box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -moz-osx-font-smoothing: grayscale;
    -webkit-transition-duration: 0.3s;
         -o-transition-duration: 0.3s;
            transition-duration: 0.3s;
    -webkit-transition-property: -webkit-transform;
    transition-property: -webkit-transform;
    -o-transition-property: transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
}

.hvr-grow:hover,
.hvr-grow:focus,
.hvr-grow:active {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}

.landing {
  width: 100vw;
  height: 100vh;
}

.landing img {
  padding-top: 15vh;
  width: 40vw;
}

#landing-logo {
  max-width: 80vw;
  width: 1000px;
  height: auto;
}

#landing-logo-mobile {
  width: 30vw;
  height: auto;
}

.landing h1 {
  color: white;
  padding-top: 7.5vh;
  font-size: 5em;
  margin: 0 auto;
}

.typewriter {
  color: #ffffff;
  font-weight: 700;
  font-family: 'tk-proxima-nova-n7'
}

.top {
    height:0;
    border-width:75px 0px 0px 100vw;
    border-style:solid;
    border-color:transparent transparent #eb7203 #eb7203;
}

.topBlack {
    height:0;
    border-width:75px 0px 0px 100vw;
    border-style:solid;
    border-color:transparent transparent #000000 #000000;
}

.topReverse {
    height:0;
    border-width: 0px 0px 75px 100vw;
    border-style:solid;
    border-color:transparent transparent #eb7203 transparent;
}

.bottom {
    height:0;
    border-width:75px 0px 0px 100vw;
    border-style:solid;
    border-color:#eb7203 #eb7203 transparent transparent;
}

.bottomReverse {
    height:0;
    border-width: 0px 0px 75px 100vw;
    border-style:solid;
    border-color:transparent transparent #eb7203 transparent;
}

.team {
  width: 100vw;
  min-height: 50vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: black;
}

.team .row {
  text-align: center;
  position: relative;
}

.team h1 {
  margin: 0px;
  padding-top: 5vh;
  color: #eb7203;
  font-family: 'tk-proxima-nova-n7';
  margin-top: 0px;
  font-weight: 700;
}

.curriculum, .newCohort {
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: #eb7203;
}

.curriculum h1, .newCohort h1 {
  margin: 0px;
  padding-top: 5vh;
  color: black;
  font-family: 'tk-proxima-nova-n7';
  margin-top: 0px;
  font-weight: 700;
}

.curriculum p, .newCohort p {
  padding-top: 1.5em;
  padding-left: 10vw;
  padding-right: 10vw;
  padding-bottom: 1.5em;
  color: black;
  font-size: 1.5em;
}

.curriculum b {
  font-family: 'tk-proxima-nova-n7';
}

.video-background {
  background: #000;
  position: fixed;
  top: 0; right: 0; bottom: 0; left: 0;
  z-index: -99;
}

.video-foreground,
.video-background iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  filter: blur(5px);
  -webkit-filter: blur(5px);
  -moz-filter: blur(5px);
  -o-filter: blur(5px);
  -ms-filter: blur(5px);
  filter: blur(5px) brightness(50%);
}

.profile {
  margin: 30px;
  width: 300px;
  height: 350px;
  display: inline-block;
  vertical-align: middle;
}

.profile-image {
  padding: 25px 25px 0px 25px;
  height: auto;
  width: inherit;
}

.downarrow {
  max-height: 50px;
  max-width: 50px;
}

.visible-mobile {
  display: none !important;
}

@media (max-width: 489px) {
  .visible-mobile {
    display: inline !important;
  }

  .hidden-mobile {
    display: none !important;
  }
}

@media (min-aspect-ratio: 16/9) {
  .video-foreground { height: 300%; top: -100%; }
}
@media (max-aspect-ratio: 16/9) {
  .video-foreground { width: 300%; left: -101%; }
}



